<template>
    <BaseField
        ref="field"

        v-bind="$attrs"
        :type="type"
        minlength="6"
        required

        v-on="$listeners"
        @update:value="checkScore"
    >
        <template #label>
            <slot name="label"/>
        </template>

        <template #actionEnd>
            <BaseButton
                type="tertiary"
                size="small"
                mode="rounded"
                class="field-password__button"
                @mousedown="hidden = false"
                @mouseleave="hidden = true"
                @mouseup="hidden = true"
                @touchstart="hidden = false"
                @touchend="hidden = true"
            >
                <font-awesome-icon
                    v-show="!hidden"
                    icon="eye"
                />
                <font-awesome-icon
                    v-show="hidden"
                    icon="eye-slash"
                />
            </BaseButton>
        </template>

        <template v-if="recommendations" #message>
            <template v-if="value">
                Сложность: {{ commentary }}
                <BaseMeter size="small" :value="meterScore"/>
            </template>
            <template v-else>
                Используйте не менее шести символов, включая буквы, цифры и специальные символы
            </template>
        </template>
    </BaseField>
</template>

<script>
import { IField } from './BaseField';

let zxcvbn;

export default {
    name: 'FieldPassword',
    extends: IField('field'),

    props: {
        recommendations: Boolean,
    },

    data() {
        return {
            hidden: true,
            meterScore: 0,
            value: null,
        };
    },

    computed: {
        type() {
            return this.hidden ? 'password' : 'text';
        },

        commentary() {
            const words = [
                'отсутствует',
                'низкая',
                'средняя',
                'хорошая',
                'высокая',
            ];

            return words[this.meterScore];
        },
    },

    created() {
        if (this.recommendations) {
            import(/* webpackChunkName: "zxcvbn" */ 'zxcvbn').then((lib) => { zxcvbn = lib.default; });
        }
    },

    methods: {
        checkScore(value) {
            if (!zxcvbn) return;

            const result = zxcvbn(value);
            this.meterScore = result.score;
            this.value = value;
        },
    },
};
</script>

<style scoped lang="less">
    .field-password {
        /deep/ &__button {
            margin-right: -5px;
        }
    }
</style>
